import React, {Component} from 'react';
import './homePage.css';
import {homepageContent} from "./../../content/content";

export class HomePage extends Component {


    goToMenu() {
        this.props.history.push(process.env.PUBLIC_URL + '/index');
    }

    render() {
        return (
            <div className="homePage">
                <div className="mainGrid">
                    <div className="message">
                        <div>
                            <div className="text big">{homepageContent.title}</div>
                            <div className="text small">{homepageContent.subtitle}</div>
                        </div>
                        <div className={`button`} onClick={() => {this.goToMenu()}}>Get in</div>
                    </div>
                </div>
            </div>
        );
    }
}
